@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: bold;
}

body {
  margin: 0;
}

.page__form {
  margin: 20px 8px 0;
}

.form {
  box-sizing: border-box;
  display: flex;
}

.input {
  box-sizing: border-box;
  color: #3d4e61;
  background-color: #fff;
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  flex-grow: 1;
  font-family: Avenir, serif;
  font-weight: 300;
  box-shadow: 0 1px 8px #3d4e611a;
}

.input::placeholder {
  color: #3d4e61;
}

.input:hover {
  box-shadow: 0 4px 4px #00000040;
}

.input:focus {
  text-shadow: 0 4px 4px #00000040;
  background-repeat: no-repeat;
  outline: none;
  font-size: 16px;
  font-weight: bold;
  line-height: 22px;
}

.input:focus::placeholder {
  opacity: 0;
}

.input--big {
